
Template.__checkName("feedback");
Template["feedback"] = new Template("Template.feedback", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n	", HTML.DIV({
      "class": "container"
    }, "\n		", HTML.DIV({
      "class": "row"
    }, "\n			", HTML.H4("online: ", Blaze.View("lookup:usersCountOnline", function() {
      return Spacebars.mustache(view.lookup("usersCountOnline"));
    }), " von ", Blaze.View("lookup:usersCount", function() {
      return Spacebars.mustache(view.lookup("usersCount"));
    }), " "), "\n			", Blaze.Each(function() {
      return Spacebars.call(view.lookup("users"));
    }, function() {
      return [ "\n				", HTML.SPAN({
        "class": function() {
          return [ "ui label ", Spacebars.mustache(view.lookup("labelClass")) ];
        }
      }, Blaze.View("lookup:email", function() {
        return Spacebars.mustache(view.lookup("email"));
      })), "\n			" ];
    }), "\n			", HTML.HR(), "\n			", HTML.H4("Fragebogen abgeschickt: ", Blaze.View("lookup:usersSurveySubmittedCount", function() {
      return Spacebars.mustache(view.lookup("usersSurveySubmittedCount"));
    }), " von ", Blaze.View("lookup:usersCount", function() {
      return Spacebars.mustache(view.lookup("usersCount"));
    }), " "), "\n			", Blaze.Each(function() {
      return Spacebars.call(view.lookup("usersSurveySubmitted"));
    }, function() {
      return [ "\n				", HTML.SPAN({
        "class": "ui label green"
      }, Blaze.View("lookup:userMail", function() {
        return Spacebars.mustache(view.lookup("userMail"));
      })), "\n			" ];
    }), "\n			", HTML.HR(), "\n		"), "\n\n		", HTML.H1("Feedback"), "\n\n		", Blaze.Each(function() {
      return Spacebars.call(view.lookup("feedback"));
    }, function() {
      return [ "\n		", HTML.DIV({
        "class": "column"
      }, "\n			", HTML.DIV({
        "class": "ui top attached block header"
      }, Blaze.View("lookup:userMail", function() {
        return Spacebars.mustache(view.lookup("userMail"));
      }), " | ", Blaze.View("lookup:createdAt", function() {
        return Spacebars.mustache(view.lookup("createdAt"));
      })), "\n			", HTML.DIV({
        "class": "ui bottom attached segment"
      }, "\n				", Blaze.View("lookup:message", function() {
        return Spacebars.mustache(view.lookup("message"));
      }), "\n			"), "\n		"), "\n		", HTML.BR(), "\n		" ];
    }), "\n	"), "\n	" ];
  }, function() {
    return [ "\n		", Spacebars.include(view.lookupTemplate("loading")), "\n	" ];
  });
}));
