
Template.__checkName("wordsAll");
Template["wordsAll"] = new Template("Template.wordsAll", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n		", Blaze.If(function() {
      return Spacebars.call(view.lookup("top5"));
    }, function() {
      return [ "\n			", HTML.H4({
        "class": "ui horizontal divider header"
      }, "\n				", HTML.I({
        "class": "users icon"
      }), "\n				Gesamt Top 5\n			"), "\n				", Blaze.Each(function() {
        return Spacebars.call(view.lookup("top5"));
      }, function() {
        return [ "\n					", HTML.P({
          "class": "top5"
        }, "\n						", HTML.DIV({
          "class": "ui horizontal statistic"
        }, "\n						  ", HTML.DIV({
          "class": "value"
        }, "\n						    ", Blaze.View("lookup:convertIndex", function() {
          return Spacebars.mustache(view.lookup("convertIndex"), view.lookup("@index"));
        }), "\n						  "), "\n						  ", HTML.DIV({
          "class": "label"
        }, "\n						    ", Blaze.View("lookup:term", function() {
          return Spacebars.mustache(view.lookup("term"));
        }), "\n						  "), "\n						"), "\n					"), "\n				" ];
      }), "\n\n		" ];
    }), "\n	" ];
  }, function() {
    return [ "\n		", Spacebars.include(view.lookupTemplate("loading")), "\n	" ];
  });
}));
