
Template.__checkName("statDevice");
Template["statDevice"] = new Template("Template.statDevice", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n	", HTML.DIV({
      "class": "ui container"
    }, "\n		", HTML.H1("Overview device usage"), "\n	"), "\n	", HTML.DIV({
      "class": "ui grid"
    }, "\n		", HTML.DIV({
      "class": "ui sixteen wide column"
    }, "\n			", Spacebars.include(view.lookupTemplate("multiBarClicksPerDevicePerDay")), "\n		"), "\n		", HTML.DIV({
      "class": "ui sixteen wide column"
    }, "\n			", Spacebars.include(view.lookupTemplate("pieClicksPerDevice")), "\n		"), "\n		", HTML.H3("TODO: Device per selected users"), "\n	"), "\n	" ];
  }, function() {
    return [ " ", Spacebars.include(view.lookupTemplate("loading")), " " ];
  });
}));
