
Template.__checkName("termDay");
Template["termDay"] = new Template("Template.termDay", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n\n			", HTML.H2("DEIN WORT DES TAGES"), "\n\n			", Blaze.If(function() {
      return Spacebars.call(view.lookup("termDay"));
    }, function() {
      return [ "\n				", Spacebars.With(function() {
        return Spacebars.call(view.lookup("termDay"));
      }, function() {
        return [ "\n				", HTML.DIV("\n					", HTML.BR(), "\n					", HTML.H1(Blaze.View("lookup:term", function() {
          return Spacebars.mustache(view.lookup("term"));
        }), Blaze.If(function() {
          return Spacebars.call(view.lookup("preposition"));
        }, function() {
          return [ " ", HTML.I(Blaze.View("lookup:preposition", function() {
            return Spacebars.mustache(view.lookup("preposition"));
          })) ];
        })), "\n				"), "\n				", HTML.BR(), "\n				", HTML.DIV({
          "class": "ui horizontal divider fadeInUp animated"
        }, "\n					", Blaze.View("lookup:wordClass", function() {
          return Spacebars.mustache(view.lookup("wordClass"));
        }), "\n				"), "\n				", HTML.DIV("\n					", HTML.DIV({
          "class": "ui selection list"
        }, "\n						", Blaze.Each(function() {
          return Spacebars.call(view.lookup("definition"));
        }, function() {
          return [ "\n						", HTML.DIV({
            "class": "left aligned"
          }, "\n							", HTML.DIV({
            "class": "ui small horizontal statistic"
          }, "\n								", HTML.DIV({
            "class": "value low"
          }, "\n									", Blaze.View("lookup:convertIndex", function() {
            return Spacebars.mustache(view.lookup("convertIndex"), view.lookup("@index"));
          }), "\n								"), "\n								", HTML.DIV({
            "class": "trainer-definition"
          }, "\n									", Blaze.View("lookup:.", function() {
            return Spacebars.mustache(view.lookup("."));
          }), "\n								"), "\n							"), "\n						"), "\n						" ];
        }), "\n					"), "\n				"), "\n					", Blaze.If(function() {
          return Spacebars.dataMustache(view.lookup("isFav"), Spacebars.dot(view.lookup("."), "_id"));
        }, function() {
          return [ "\n						", HTML.DIV({
            "class": "btn btn-delete flipInY animated"
          }, "\n							", HTML.I({
            "class": "heart icon huge red"
          }), "\n						"), "\n					" ];
        }, function() {
          return [ "\n						", HTML.DIV({
            "class": "btn btn-insert flipInY animated"
          }, "\n							", HTML.I({
            "class": "empty heart icon huge red"
          }), "\n						"), "\n					" ];
        }), "\n				" ];
      }), "\n			" ];
    }, function() {
      return [ "\n				", HTML.H1("Ups!"), "\n			" ];
    }), "\n\n	" ];
  }, function() {
    return [ "\n		", Spacebars.include(view.lookupTemplate("loading")), "\n	" ];
  });
}));
