
Template.__checkName("bar");
Template["bar"] = new Template("Template.bar", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row"
  }, "\n    ", HTML.DIV({
    "class": function() {
      return [ "ui bar ", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "attentionMode");
      }, function() {
        return "olive";
      }, function() {
        return "orange";
      }), " one item inverted fixed menu attention-mode" ];
    }
  }, "\n      ", HTML.A({
    "class": "item center aligned",
    href: function() {
      return Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "attentionMode");
      }, function() {
        return Blaze.View("lookup:getSession", function() {
          return Spacebars.mustache(view.lookup("getSession"), "lastPath");
        });
      }, function() {
        return "/low";
      });
    }
  }, "\n        ", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "attentionMode");
  }, function() {
    return HTML.I({
      "class": "fa fa-angle-up fa-4x"
    });
  }, function() {
    return [ HTML.I({
      "class": "fa fa-angle-down fa-4x"
    }), " " ];
  }), "\n      "), "\n    "), "\n  ");
}));
