
Template.__checkName("low");
Template["low"] = new Template("Template.low", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n  ", HTML.DIV({
      "class": "low"
    }, "\n    ", HTML.SECTION({
      "class": "some-area fill-height-or-more"
    }, "\n\n            ", Spacebars.include(view.lookupTemplate("backwardLow")), "\n\n          ", HTML.DIV({
      "class": "content-entry"
    }, "\n\n              ", Spacebars.With(function() {
      return Spacebars.call(view.lookup("entry"));
    }, function() {
      return [ "\n                ", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "lesen");
      }, function() {
        return [ "\n                  ", Spacebars.include(view.lookupTemplate("modusLesenLow")), "\n                " ];
      }), "\n                ", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "wort");
      }, function() {
        return [ "\n                  ", Spacebars.include(view.lookupTemplate("modusWortLow")), "\n                " ];
      }), "\n                ", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "definition");
      }, function() {
        return [ "\n                  ", Spacebars.include(view.lookupTemplate("modusDefinitionLow")), "\n                " ];
      }), "\n\n                ", Blaze.If(function() {
        return Spacebars.call(view.lookup("lengthIsOne"));
      }, function() {
        return [ "\n                  ", HTML.DIV({
          "class": "ui orange message text-center"
        }, "\n                      ", HTML.SPAN({
          "class": "flag animate-box fadeInUp animated"
        }, "\n                        ", Blaze.If(function() {
          return Spacebars.dataMustache(view.lookup("getSession"), "navSourceFav");
        }, function() {
          return [ "\n                          ", HTML.H3("Letzter Favorit ", HTML.I({
            "class": "icon frown"
          })), "\n                        " ];
        }), "\n                        ", Blaze.If(function() {
          return Spacebars.dataMustache(view.lookup("getSession"), "navSourceNotFav");
        }, function() {
          return [ "\n                          ", HTML.H3("Letzter Begriff der nicht favorisiert ist."), "\n                        " ];
        }), "\n                      "), "\n                  "), "\n                " ];
      }), "\n\n                ", Spacebars.include(view.lookupTemplate("navLow")), "\n\n              " ];
    }), "\n\n          "), "\n\n          ", Spacebars.include(view.lookupTemplate("forwardLow")), "\n\n    "), "\n  "), "\n" ];
  }, function() {
    return [ "\n  ", HTML.DIV({
      "class": "ui container low"
    }, "\n    ", Spacebars.include(view.lookupTemplate("loading")), "\n  "), "\n\n\n" ];
  });
}));
