
Template.__checkName("backwardLow");
Template["backwardLow"] = new Template("Template.backwardLow", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("lengthIsOne"));
  }, function() {
    return [ "\n		", Spacebars.include(view.lookupTemplate("stopLow")), "\n	" ];
  }, function() {
    return [ "\n		", Spacebars.With(function() {
      return Spacebars.call(view.lookup("entry"));
    }, function() {
      return [ "\n				", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "navOrderRandom");
      }, function() {
        return [ "\n					", Spacebars.include(view.lookupTemplate("stopLow")), "\n				" ];
      }, function() {
        return [ "\n\n				", Blaze.If(function() {
          return Spacebars.call(view.lookup("screensize_small"));
        }, function() {
          return [ "\n					", Spacebars.include(view.lookupTemplate("backwardLowSmall")), "\n				" ];
        }), "\n				", Blaze.If(function() {
          return Spacebars.call(view.lookup("screensize_medium"));
        }, function() {
          return [ "\n					", Spacebars.include(view.lookupTemplate("backwardLowSmall")), "\n				" ];
        }), "\n				", Blaze.If(function() {
          return Spacebars.call(view.lookup("screensize_large"));
        }, function() {
          return [ "\n					", Spacebars.include(view.lookupTemplate("backwardLowLarge")), "\n				" ];
        }), "\n				", Blaze.If(function() {
          return Spacebars.call(view.lookup("screensize_xlarge"));
        }, function() {
          return [ "\n					", Spacebars.include(view.lookupTemplate("backwardLowLarge")), "\n				" ];
        }), "\n				", Blaze.If(function() {
          return Spacebars.call(view.lookup("screensize_xxlarge"));
        }, function() {
          return [ "\n					", Spacebars.include(view.lookupTemplate("backwardLowLarge")), "\n				" ];
        }), "\n\n				" ];
      }), "\n		" ];
    }), "\n	" ];
  });
}));

Template.__checkName("backwardLowSmall");
Template["backwardLowSmall"] = new Template("Template.backwardLowSmall", (function() {
  var view = this;
  return HTML.DIV({
    "class": "btn-browse-low gradient-left-to-right btn-backward"
  }, HTML.Raw('\n		<div class="ui row low">\n			<div class="ui icon header low">\n				<i class="step backward icon"></i>\n				<div class="content"></div>\n			</div>\n		</div>\n		'), Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "navOrderRandom");
  }, function() {
    return [ "\n			", HTML.DIV({
      "class": "ui row low center aligned"
    }, "\n				", HTML.DIV({
      "class": "ui header"
    }, "\n					", HTML.I({
      "class": "random icon low"
    }), "\n				"), "\n			"), "\n		" ];
  }, function() {
    return [ "\n			", HTML.DIV({
      "class": "ui row low center aligned"
    }, "\n				", HTML.DIV({
      "class": "ui header"
    }, "\n					", HTML.I({
      "class": "long arrow left icon low"
    }), "\n				"), "\n			"), "\n		" ];
  }), "\n	");
}));

Template.__checkName("backwardLowLarge");
Template["backwardLowLarge"] = new Template("Template.backwardLowLarge", (function() {
  var view = this;
  return HTML.Raw('<div class="btn-browse-low gradient-left-to-right btn-backward">\n		<div class="ui row low">\n			<div class="ui icon header low">\n				<i class="step backward icon"></i>\n				<div class="content">\n					ZURÃœCK\n				</div>\n			</div>\n		</div>\n		<div class="ui row low">\n			<div class="ui header">\n				<i class="long arrow left icon low"></i>\n				<div class="content">\n					<div class="sub header low">A - Z</div>\n				</div>\n			</div>\n		</div>\n	</div>');
}));

Template.__checkName("stopLow");
Template["stopLow"] = new Template("Template.stopLow", (function() {
  var view = this;
  return HTML.DIV({
    "class": "btn-ban-low"
  }, "\n		", HTML.DIV({
    "class": "ui row low"
  }, "\n			", HTML.DIV({
    "class": "ui icon header low disabled"
  }, "\n				", HTML.Raw('<i class="ban icon"></i>'), "\n				", HTML.DIV({
    "class": "content"
  }, "\n					", Blaze.If(function() {
    return Spacebars.call(view.lookup("screensize_large"));
  }, function() {
    return "\n						STOP\n					";
  }), "\n					", Blaze.If(function() {
    return Spacebars.call(view.lookup("screensize_xlarge"));
  }, function() {
    return "\n						STOP\n					";
  }), "\n					", Blaze.If(function() {
    return Spacebars.call(view.lookup("screensize_xxlarge"));
  }, function() {
    return "\n						STOP\n					";
  }), "\n				"), "\n			"), "\n		"), "\n	");
}));
