
Template.__checkName("backward");
Template["backward"] = new Template("Template.backward", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row"
  }, "\n		", Blaze.If(function() {
    return Spacebars.call(view.lookup("lengthIsOne"));
  }, function() {
    return [ "\n		", HTML.DIV({
      "class": "text-center spaced"
    }, "\n			", HTML.BUTTON({
      "class": "ui icon header button disabled"
    }, "\n					", HTML.I({
      "class": "ban icon"
    }), "\n			"), "\n		"), "\n		" ];
  }, function() {
    return [ "\n			", Blaze.Unless(function() {
      return Spacebars.dataMustache(view.lookup("getSession"), "navOrderRandom");
    }, function() {
      return [ "\n				", Spacebars.With(function() {
        return Spacebars.call(view.lookup("entry"));
      }, function() {
        return [ "\n					", HTML.DIV({
          "class": "text-center spaced btn-backward"
        }, "\n						", Blaze.If(function() {
          return Spacebars.call(view.lookup("screensize_small"));
        }, function() {
          return [ "\n							", Spacebars.include(view.lookupTemplate("backwardSmall")), "\n						" ];
        }), "\n						", Blaze.If(function() {
          return Spacebars.call(view.lookup("screensize_medium"));
        }, function() {
          return [ "\n							", Spacebars.include(view.lookupTemplate("backwardSmall")), "\n						" ];
        }), "\n						", Blaze.If(function() {
          return Spacebars.call(view.lookup("screensize_large"));
        }, function() {
          return [ "\n							", Spacebars.include(view.lookupTemplate("backwardLarge")), "\n						" ];
        }), "\n						", Blaze.If(function() {
          return Spacebars.call(view.lookup("screensize_xlarge"));
        }, function() {
          return [ "\n							", Spacebars.include(view.lookupTemplate("backwardLarge")), "\n						" ];
        }), "\n						", Blaze.If(function() {
          return Spacebars.call(view.lookup("screensize_xxlarge"));
        }, function() {
          return [ "\n							", Spacebars.include(view.lookupTemplate("backwardLarge")), "\n						" ];
        }), "\n					"), "\n				" ];
      }), "\n			" ];
    }, function() {
      return [ "\n			", HTML.DIV({
        "class": "text-center spaced"
      }, "\n				", HTML.BUTTON({
        "class": "ui icon header button disabled"
      }, "\n						", HTML.I({
        "class": "ban icon"
      }), "\n				"), "\n			"), "\n			" ];
    }), "\n		" ];
  }), "\n	");
}));

Template.__checkName("backwardSmall");
Template["backwardSmall"] = new Template("Template.backwardSmall", (function() {
  var view = this;
  return HTML.Raw('<button class="ui icon huge button">\n		<i class="step backward icon big"></i>\n			<h5>A - Z</h5>\n	</button>');
}));

Template.__checkName("backwardLarge");
Template["backwardLarge"] = new Template("Template.backwardLarge", (function() {
  var view = this;
  return HTML.Raw('<button class="ui icon header button">\n			<i class="step backward icon"></i>\n			<div class="content">\n				ZURÃœCK\n				<div class="sub header">A - Z</div>\n			</div>\n	</button>');
}));
