
Template.__checkName("modusLesen");
Template["modusLesen"] = new Template("Template.modusLesen", (function() {
  var view = this;
  return HTML.DIV({
    "class": "ui center aligned segment"
  }, "\n\n		", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("isFav"), Spacebars.dot(view.lookup("."), "_id"));
  }, function() {
    return [ "\n				", HTML.DIV({
      "class": "btn-delay btn-delete ui corner red massive label"
    }, "\n					", HTML.DIV({
      "class": "flipInY animated"
    }, "\n						", HTML.I({
      "class": function() {
        return [ "icon-fav-", Spacebars.mustache(view.lookup("_id")), " fa fa-heart" ];
      }
    }), "\n					"), "\n				"), "\n		" ];
  }, function() {
    return [ "\n			", HTML.DIV({
      "class": "btn-delay btn-insert ui corner red massive label"
    }, "\n				", HTML.DIV({
      "class": "flipInY animated"
    }, "\n					", HTML.I({
      "class": function() {
        return [ "icon-not-fav-", Spacebars.mustache(view.lookup("_id")), " fa fa-heart-o" ];
      }
    }), "\n				"), "\n			"), "\n		" ];
  }), HTML.Raw('\n\n		<!-- {{#if isFav this._id}}\n		<div class="btn btn-delete flipInY animated">\n			<i class="icon-fav-{{_id}} fa fa-heart fa-4x"></i>\n		</div>\n		{{else}}\n		<div class="btn btn-insert flipInY animated">\n			<i class="icon-not-fav-{{_id}} fa fa-heart-o fa-4x"></i>\n		</div>\n		{{/if}} -->\n\n\n		'), HTML.DIV("\n			", HTML.Raw("<br>"), "\n			", HTML.H1(Blaze.View("lookup:term", function() {
    return Spacebars.mustache(view.lookup("term"));
  }), Blaze.If(function() {
    return Spacebars.call(view.lookup("preposition"));
  }, function() {
    return [ " ", HTML.I(Blaze.View("lookup:preposition", function() {
      return Spacebars.mustache(view.lookup("preposition"));
    })) ];
  })), "\n			", HTML.Raw("<br>"), "\n		"), "\n		", HTML.DIV({
    "class": "ui horizontal divider fadeInUp animated"
  }, "\n			", Blaze.View("lookup:wordClass", function() {
    return Spacebars.mustache(view.lookup("wordClass"));
  }), "\n		"), "\n		", HTML.DIV("\n			", HTML.Raw("<br>"), "\n			", HTML.DIV({
    "class": "ui selection list"
  }, "\n				", Blaze.Each(function() {
    return Spacebars.call(view.lookup("definition"));
  }, function() {
    return [ "\n				", HTML.DIV({
      "class": "left aligned"
    }, "\n					", HTML.DIV({
      "class": "ui small horizontal statistic"
    }, "\n						", HTML.DIV({
      "class": "value low"
    }, "\n							", Blaze.View("lookup:convertIndex", function() {
      return Spacebars.mustache(view.lookup("convertIndex"), view.lookup("@index"));
    }), "\n						"), "\n						", HTML.DIV({
      "class": "trainer-definition high"
    }, "\n							", Blaze.View("lookup:.", function() {
      return Spacebars.mustache(view.lookup("."));
    }), "\n						"), "\n					"), "\n				"), "\n				" ];
  }), "\n			"), "\n			", HTML.Raw("<br>"), "\n		"), "\n\n	");
}));
