
Template.__checkName("navSourceTrainer");
Template["navSourceTrainer"] = new Template("Template.navSourceTrainer", (function() {
  var view = this;
  return [ HTML.Raw("<br>\n	"), HTML.DIV({
    "class": "text-center spaced"
  }, "\n		", HTML.Raw("<h3>Quelle</h3>"), "\n\n		", HTML.BUTTON({
    "class": function() {
      return [ "ui fluid large button btn-source-all ", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "navSourceAll");
      }, function() {
        return "active primary";
      }) ];
    }
  }, HTML.Raw('<i class="icon empty heart"></i>'), HTML.Raw('<i class="icon plus"></i>'), HTML.Raw('<i class="icon heart"></i>'), " Alle"), "\n		", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "lengthFav");
  }, function() {
    return [ "\n		", HTML.BUTTON({
      "class": function() {
        return [ "ui fluid large button btn-source-fav ", Blaze.If(function() {
          return Spacebars.dataMustache(view.lookup("getSession"), "navSourceFav");
        }, function() {
          return "active primary";
        }) ];
      }
    }, "\n			", HTML.I({
      "class": "icon heart"
    }), "\n				Favoriten\n				", HTML.SPAN({
      "class": "ui label"
    }, Blaze.View("lookup:getSession", function() {
      return Spacebars.mustache(view.lookup("getSession"), "lengthFav");
    })), "\n		"), "\n\n			", Blaze.If(function() {
      return Spacebars.dataMustache(view.lookup("getSession"), "lengthNotFav");
    }, function() {
      return [ "\n			", HTML.BUTTON({
        "class": function() {
          return [ "ui fluid large button btn-source-not-fav ", Blaze.If(function() {
            return Spacebars.dataMustache(view.lookup("getSession"), "navSourceNotFav");
          }, function() {
            return "active primary";
          }) ];
        }
      }, "\n				", HTML.I({
        "class": "icon empty heart"
      }), "\n				Alle ohne Favoriten\n				", HTML.DIV({
        "class": "ui label"
      }, Blaze.View("lookup:getSession", function() {
        return Spacebars.mustache(view.lookup("getSession"), "lengthNotFav");
      })), "\n			"), "\n			" ];
    }), "\n\n		" ];
  }), "\n\n	") ];
}));
