
Template.__checkName("nav");
Template["nav"] = new Template("Template.nav", (function() {
  var view = this;
  return HTML.DIV({
    "class": "ui inverted segment"
  }, "\n    ", HTML.DIV({
    "class": "ui container nav-main"
  }, "\n      ", HTML.DIV({
    "class": "row"
  }, "\n        ", HTML.DIV({
    "class": function() {
      return [ "ui inverted fluid ", Blaze.If(function() {
        return Spacebars.call(view.lookup("surveySubmitted"));
      }, function() {
        return "three";
      }, function() {
        return "four";
      }), " item large menu" ];
    }
  }, "\n          ", HTML.A({
    "class": function() {
      return [ "item ", Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "index"
      })) ];
    },
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "index");
    }
  }, "Start"), "\n          ", HTML.A({
    "class": function() {
      return [ "item ", Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "trainer"
      })) ];
    },
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "trainer");
    }
  }, "Trainer"), "\n          ", HTML.A({
    "class": function() {
      return [ "item ", Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "register"
      })) ];
    },
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "register");
    }
  }, "Register"), "\n          ", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "surveyUnlocked");
  }, function() {
    return [ "\n            ", Blaze.Unless(function() {
      return Spacebars.call(view.lookup("surveySubmitted"));
    }, function() {
      return [ "\n              ", HTML.A({
        "class": function() {
          return [ "item ", Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
            regex: "fragebogen"
          })) ];
        },
        href: function() {
          return Spacebars.mustache(view.lookup("pathFor"), "fragebogen");
        }
      }, "Fragebogen"), "\n            " ];
    }), "\n          " ];
  }), "\n        "), "\n      "), "\n    "), "\n  ");
}));
