
Template.__checkName("forward");
Template["forward"] = new Template("Template.forward", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row"
  }, "\n		", Blaze.If(function() {
    return Spacebars.call(view.lookup("lengthIsOne"));
  }, function() {
    return [ "\n			", HTML.DIV({
      "class": "text-center spaced"
    }, "\n				", HTML.BUTTON({
      "class": "ui icon header button disabled"
    }, "\n						", HTML.I({
      "class": "ban icon"
    }), "\n				"), "\n			"), "\n		" ];
  }, function() {
    return [ "\n			", Spacebars.With(function() {
      return Spacebars.call(view.lookup("entry"));
    }, function() {
      return [ "\n				", HTML.DIV({
        "class": "text-center spaced btn-forward"
      }, "\n					", Blaze.If(function() {
        return Spacebars.call(view.lookup("screensize_small"));
      }, function() {
        return [ "\n						", Spacebars.include(view.lookupTemplate("forwardSmall")), "\n					" ];
      }), "\n					", Blaze.If(function() {
        return Spacebars.call(view.lookup("screensize_medium"));
      }, function() {
        return [ "\n						", Spacebars.include(view.lookupTemplate("forwardSmall")), "\n					" ];
      }), "\n					", Blaze.If(function() {
        return Spacebars.call(view.lookup("screensize_large"));
      }, function() {
        return [ "\n						", Spacebars.include(view.lookupTemplate("forwardLarge")), "\n					" ];
      }), "\n					", Blaze.If(function() {
        return Spacebars.call(view.lookup("screensize_xlarge"));
      }, function() {
        return [ "\n						", Spacebars.include(view.lookupTemplate("forwardLarge")), "\n					" ];
      }), "\n					", Blaze.If(function() {
        return Spacebars.call(view.lookup("screensize_xxlarge"));
      }, function() {
        return [ "\n						", Spacebars.include(view.lookupTemplate("forwardLarge")), "\n					" ];
      }), "\n				"), "\n			" ];
    }), "\n		" ];
  }), "\n	");
}));

