
Template.__checkName("statFav");
Template["statFav"] = new Template("Template.statFav", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n	", HTML.DIV({
      "class": "ui container"
    }, "\n		", HTML.H1("Favourising behaviour in varations 'compact mode', 'trainer' and 'register'"), "\n	"), "\n		", HTML.DIV({
      "class": "ui grid"
    }, "\n			", HTML.DIV({
      "class": "ui sixteen wide column"
    }, "\n				", Spacebars.include(view.lookupTemplate("multiBarFavsPerVariantPerDay")), "\n			"), "\n			", HTML.DIV({
      "class": "ui sixteen wide column"
    }, "\n				", Spacebars.include(view.lookupTemplate("lineFavPerVariantPerDayPerDevice")), "\n			"), "\n			", HTML.DIV({
      "class": "ui sixteen wide column"
    }, "\n				", Spacebars.include(view.lookupTemplate("pieFavsPerVariant")), "\n			"), "\n		"), "\n		", HTML.H3("TODO: FAV per selected users"), "\n	" ];
  }, function() {
    return [ "\n		", Spacebars.include(view.lookupTemplate("loading")), "\n	" ];
  });
}));
