
Template.__checkName("statTotal");
Template["statTotal"] = new Template("Template.statTotal", (function() {
  var view = this;
  return [ HTML.DIV({
    "class": "ui container"
  }, "\n		", HTML.Raw("<h1>Gesamt</h1>"), "\n		", HTML.H4("Gesetzte Datenpunkte (Clickevents): ", Blaze.View("lookup:dataPointsCount", function() {
    return Spacebars.mustache(view.lookup("dataPointsCount"));
  })), "\n	"), "\n	", HTML.DIV({
    "class": "ui grid"
  }, "\n		", HTML.DIV({
    "class": "sixteen wide column"
  }, "\n			", Spacebars.include(view.lookupTemplate("barClicksPerDay")), "\n		"), "\n	") ];
}));
