
Template.__checkName("navSource");
Template["navSource"] = new Template("Template.navSource", (function() {
  var view = this;
  return [ HTML.Raw("<br>\n		"), HTML.DIV({
    "class": "text-center spaced"
  }, "\n			", HTML.Raw("<h3>Quelle</h3>"), "\n			", HTML.BUTTON({
    "class": "ui fluid huge button btn-source active primary"
  }, "\n				", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "navSourceAll");
  }, function() {
    return [ "\n					", HTML.I({
      "class": "icon empty heart"
    }), HTML.I({
      "class": "icon plus"
    }), HTML.I({
      "class": "icon heart"
    }), " Alle\n				" ];
  }), "\n				", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "navSourceNotFav");
  }, function() {
    return [ "\n					", HTML.I({
      "class": "icon empty heart"
    }), " Alle ohne Favoriten\n					", HTML.SPAN({
      "class": "ui label"
    }, Blaze.View("lookup:getSession", function() {
      return Spacebars.mustache(view.lookup("getSession"), "lengthNotFav");
    })), "\n				" ];
  }), "\n				", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "navSourceFav");
  }, function() {
    return [ "\n					", HTML.I({
      "class": "icon heart"
    }), " Favoriten\n					", HTML.SPAN({
      "class": "ui label"
    }, Blaze.View("lookup:getSession", function() {
      return Spacebars.mustache(view.lookup("getSession"), "lengthFav");
    })), "\n				" ];
  }), "\n			"), "\n			", Blaze.Unless(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "lengthFav");
  }, function() {
    return [ "\n			", HTML.DIV({
      "class": "ui red message text-center"
    }, "\n				Keine Favoriten gespeichert.\n			"), "\n			" ];
  }), "\n		") ];
}));
