
Template.__checkName("navLow");
Template["navLow"] = new Template("Template.navLow", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row"
  }, HTML.Raw('\n		<!-- Button trigger modal -->\n		<div class="btn-settings-low">\n			<div id="call-modal-settings" class="ui icon header low btn-browse-low">\n				<i class="setting icon"></i>\n				<div class="content">\n					EINSTELLUNGEN\n				</div>\n			</div>\n		</div>\n\n\n		'), HTML.DIV({
    id: "modal-settings",
    "class": "ui basic modal"
  }, "\n		  ", HTML.Raw('<i class="close big icon fluid"></i>'), "\n		  ", HTML.Raw('<div class="header">\n		    Einstellungen\n		  </div>'), "\n			", HTML.DIV({
    "class": "content"
  }, "\n				", Spacebars.include(view.lookupTemplate("navMode")), "\n				", Spacebars.include(view.lookupTemplate("navSource")), "\n				", Spacebars.include(view.lookupTemplate("navOrderTrainer")), "\n		  "), "\n		"), HTML.Raw('\n\n		<!-- Modal -->\n		<!-- <div class="modal fade" id="settingsLow" tabindex="-1" role="dialog" aria-labelledby="settingsLowLabel">\n			<div class="modal-dialog" role="document">\n				<div class="modal-content">\n					<div class="modal-header">\n						<button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>\n						<h4 class="modal-title control-label" id="settingsLowLabel">Einstellungen</h4>\n					</div>\n					<div class="modal-body">\n						{{> navMode }}\n						{{> navSource }}\n						{{> navOrderTrainer }}\n					</div>\n\n				</div>\n			</div>\n		</div> -->\n	'));
}));
