
Template.__checkName("statBrowse");
Template["statBrowse"] = new Template("Template.statBrowse", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n	", HTML.DIV({
      "class": "ui container"
    }, "\n		", HTML.H1("Browsing behaviour in variations 'compact mode' and 'trainer'"), "\n	"), "\n	", HTML.DIV({
      "class": "ui grid"
    }, "\n		", HTML.DIV({
      "class": "sixteen wide column"
    }, "\n			", Spacebars.include(view.lookupTemplate("multiBarBrowsePerVariantPerDay")), "\n		"), "\n		", HTML.DIV({
      "class": "ui sixteen wide column"
    }, "\n			", Spacebars.include(view.lookupTemplate("lineBrowsePerVariantPerDayPerDevice")), "\n		"), "\n		", HTML.DIV({
      "class": "ui sixteen wide column"
    }, "\n			", Spacebars.include(view.lookupTemplate("pieBrowsePerVariant")), "\n		"), "\n		", HTML.H3("TODO: Browse per selected users"), "\n	"), "\n	" ];
  }, function() {
    return [ " ", Spacebars.include(view.lookupTemplate("loading")), " " ];
  });
}));
