
Template.__checkName("navOverview");
Template["navOverview"] = new Template("Template.navOverview", (function() {
  var view = this;
  return HTML.DIV({
    "class": "container ui"
  }, HTML.Raw("\n\n			<br>\n			"), HTML.DIV({
    "class": "ui two item menu"
  }, "\n			  ", HTML.A({
    "class": function() {
      return [ Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "statistik"
      })), " item" ];
    },
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "statistik");
    }
  }, "Statistik"), "\n			  ", HTML.A({
    "class": function() {
      return [ Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "feedback"
      })), " item" ];
    },
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "feedback");
    }
  }, "Feedback"), "\n			"), HTML.Raw("\n			<br>\n			<br>\n\n	"));
}));
