
Template.__checkName("register");
Template["register"] = new Template("Template.register", (function() {
  var view = this;
  return [ HTML.Raw('<div class="ui container">\n      <br>\n      <div class="ui fluid input icon">\n        <input autofocus="" type="text" name="search" placeholder="Finde einen Begriff...">\n        <i class="search icon big"></i>\n      </div>\n      <br>\n    </div>\n    <!-- <div class="ui container">\n      {{> paginationBar itemsPerPage=3 window=5 count=\'total_posts\'}}\n    </div> -->\n    '), HTML.DIV({
    "class": function() {
      return [ "ui ", Blaze.If(function() {
        return Spacebars.call(view.lookup("query"));
      }, function() {
        return "container";
      }), " " ];
    }
  }, "\n      ", Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n        ", Blaze.If(function() {
      return Spacebars.call(view.lookup("searching"));
    }, function() {
      return [ "\n          ", Spacebars.include(view.lookupTemplate("loading")), "\n        " ];
    }, function() {
      return [ "\n            ", HTML.DIV({
        "class": function() {
          return [ "ui relaxed list ", Blaze.Unless(function() {
            return Spacebars.call(view.lookup("query"));
          }, function() {
            return "register-list";
          }), " " ];
        }
      }, "\n            ", Blaze.Each(function() {
        return Spacebars.call(view.lookup("vocabulary"));
      }, function() {
        return [ "\n              ", Blaze.If(function() {
          return Spacebars.call(view.lookup("query"));
        }, function() {
          return [ "\n                ", HTML.DIV({
            "class": "item"
          }, "\n                  ", HTML.H3({
            "class": "ui dividing header"
          }, "\n                    ", Blaze.View("lookup:letter", function() {
            return Spacebars.mustache(view.lookup("letter"));
          }), "\n                  "), "\n\n                  ", Spacebars.include(view.lookupTemplate("letterCollapse")), "\n                "), "\n              " ];
        }, function() {
          return [ "\n                ", HTML.BLOCKQUOTE("\n                  ", HTML.DIV({
            "class": "item"
          }, "\n                    ", HTML.H3({
            "class": "ui dividing header"
          }, "\n                      ", Blaze.View("lookup:letter", function() {
            return Spacebars.mustache(view.lookup("letter"));
          }), "\n                    "), "\n\n                    ", Spacebars.include(view.lookupTemplate("letterCollapse")), "\n                  "), "\n                "), "\n              " ];
        }), "\n            " ];
      }, function() {
        return [ "\n              ", HTML.P({
          "class": "ui orange message"
        }, 'Nichts gefunden unter dem Begriff " ', Blaze.View("lookup:query", function() {
          return Spacebars.mustache(view.lookup("query"));
        }), ' ".'), "\n            " ];
      }), "\n            "), "\n        " ];
    }), "\n      " ];
  }, function() {
    return [ "\n        ", Spacebars.include(view.lookupTemplate("loading")), "\n      " ];
  }), "\n\n\n\n          ", HTML.Raw('<!-- {{#if Template.subscriptionsReady}}\n            {{#if searching}}\n              <ul class="list-group">\n                {{#if searching}}\n\n                {{else}}\n                  <p class="ui orange message">Nichts gefunden unter dem Begriff " {{query}} ".</p>\n                {{/if}}\n              </ul>\n\n            {{else}}\n\n              <div class="ui styled fluid accordion">\n                {{#each vocabulary}}\n                <div class="title">\n                  <i class="dropdown icon"></i>\n                  {{letter}}\n                </div>\n\n                <div class="content">\n                  <p class="transition hidden">\n                    {{>letterCollapse}}\n                  </p>\n                </div>\n                {{/each}}\n              </div>\n            {{/if}}\n          {{else}}\n            {{> loading}}\n          {{/if}} -->'), "\n\n\n  ") ];
}));
