
Template.__checkName("index");
Template["index"] = new Template("Template.index", (function() {
  var view = this;
  return [ HTML.DIV({
    "class": "ui container"
  }, "\n    ", HTML.DIV({
    "class": "ui one column grid"
  }, "\n      ", HTML.DIV({
    "class": "column"
  }, "\n        ", HTML.DIV({
    "class": "ui labeled button logout",
    tabindex: "0"
  }, "\n          ", Spacebars.include(view.lookupTemplate("atNavButton")), "\n          ", HTML.DIV({
    "class": "ui basic label"
  }, "\n                    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("currentUser"));
  }, function() {
    return [ "Angemeldet als: ", Blaze.View("lookup:currentUserMail", function() {
      return Spacebars.mustache(view.lookup("currentUserMail"));
    }), " " ];
  }), "\n                  "), "\n        "), "\n        ", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("isInRole"), "admin");
  }, function() {
    return [ "\n        ", HTML.A({
      "class": function() {
        return Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
          regex: "verwaltung"
        }));
      },
      href: function() {
        return Spacebars.mustache(view.lookup("pathFor"), "verwaltung");
      }
    }, "\n          ", HTML.BUTTON({
      "class": "ui button"
    }, "Verwaltung"), "\n        "), "\n        " ];
  }), "\n      "), "\n\n    "), "\n  "), HTML.Raw("\n\n  <br>\n  <br>\n\n  "), Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "surveyUnlocked");
  }, function() {
    return [ "\n    ", Blaze.Unless(function() {
      return Spacebars.call(view.lookup("surveySubmitted"));
    }, function() {
      return [ "\n      ", HTML.DIV({
        "class": "ui container two column stackable grid"
      }, "\n        ", HTML.DIV({
        "class": "ten wide column center aligned"
      }, "\n          ", Spacebars.include(view.lookupTemplate("termDay")), "\n        "), "\n        ", HTML.DIV({
        "class": "six wide column"
      }, "\n          ", Spacebars.include(view.lookupTemplate("msgFragebogen")), "\n        "), "\n      "), "\n      " ];
    }, function() {
      return [ "\n      ", HTML.DIV({
        "class": "ui text container"
      }, "\n        ", Spacebars.include(view.lookupTemplate("termDay")), "\n      "), "\n    " ];
    }), "\n  " ];
  }), "\n    ", HTML.DIV({
    "class": "ui container two column stackable grid"
  }, "\n      ", HTML.DIV({
    "class": "ten wide column"
  }, "\n        ", Spacebars.include(view.lookupTemplate("docWidget")), "\n\n      "), "\n      ", HTML.DIV({
    "class": "six wide column"
  }, "\n\n        ", Spacebars.include(view.lookupTemplate("wordsAll")), "\n        ", Spacebars.include(view.lookupTemplate("wordsUser")), "\n      "), "\n    "), "\n    ", HTML.DIV({
    "class": "ui container one column grid"
  }, "\n      ", HTML.DIV({
    "class": "sixteen wide column"
  }, "\n        ", Spacebars.include(view.lookupTemplate("feedbackClient")), "\n      "), "\n    ") ];
}));
