
Template.__checkName("statUsers");
Template["statUsers"] = new Template("Template.statUsers", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n	", HTML.DIV({
      "class": "ui container"
    }, "\n		", HTML.H1("Users"), "\n	"), "\n	", HTML.DIV({
      "class": "ui grid"
    }, "\n		", HTML.DIV({
      "class": "ui sixteen wide column"
    }, "\n			", Spacebars.include(view.lookupTemplate("lineUsersPerDay")), "\n		"), "\n		", HTML.Comment(' <div class="ui sixteen wide column">\n			{{> chartLineOnlineUser }}\n		</div> '), "\n		", HTML.DIV({
      "class": "ui sixteen wide column"
    }, "\n			", Spacebars.include(view.lookupTemplate("pieClicksPerUser")), "\n		"), "\n		", HTML.DIV({
      "class": "ui sixteen wide column"
    }, "\n			", Spacebars.include(view.lookupTemplate("pieAVGPerUserPerDevice")), "\n		"), "\n	"), "\n	" ];
  }, function() {
    return [ " ", Spacebars.include(view.lookupTemplate("loading")), " " ];
  });
}));
