
Template.__checkName("entry");
Template["entry"] = new Template("Template.entry", (function() {
  var view = this;
  return [ HTML.Raw("<!-- <blockquote> -->\n	"), HTML.DIV({
    "class": "item register-item"
  }, "\n		", HTML.DIV({
    "class": "ui icon big"
  }, "\n			", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("isFav"), Spacebars.dot(view.lookup("."), "_id"));
  }, function() {
    return [ "\n				", HTML.DIV({
      "class": "btn-register btn-delete fadeIn animated"
    }, "\n						", HTML.I({
      "class": "icon red heart big"
    }), "\n				"), "\n			" ];
  }, function() {
    return [ "\n				", HTML.DIV({
      "class": "btn-register btn-insert fadeIn animated"
    }, "\n						", HTML.I({
      "class": "icon red empty heart big"
    }), "\n				"), "\n			" ];
  }), "\n		"), "\n		", HTML.DIV({
    "class": "content middle aligned"
  }, "\n			", HTML.DIV({
    "class": "header"
  }, "\n				", Blaze.View("lookup:term", function() {
    return Spacebars.mustache(view.lookup("term"));
  }), " ", HTML.I(" ", Blaze.View("lookup:preposition", function() {
    return Spacebars.mustache(view.lookup("preposition"));
  })), "\n			"), "\n			", HTML.DIV({
    "class": "meta"
  }, "\n				", Blaze.View("lookup:wordClass", function() {
    return Spacebars.mustache(view.lookup("wordClass"));
  }), "\n			"), "\n			", HTML.DIV({
    "class": "ui ordered list"
  }, "\n				", Blaze.Each(function() {
    return Spacebars.call(view.lookup("definition"));
  }, function() {
    return [ "\n					", HTML.DIV({
      "class": "item"
    }, "\n						", Blaze.View("lookup:.", function() {
      return Spacebars.mustache(view.lookup("."));
    }), "\n					"), "\n				" ];
  }), "\n			"), "\n		"), "\n	"), HTML.Raw("\n	<!-- </blockquote> -->") ];
}));
