
Template.__checkName("navMode");
Template["navMode"] = new Template("Template.navMode", (function() {
  var view = this;
  return [ HTML.Raw("<br>\n		"), HTML.DIV({
    "class": "text-center spaced"
  }, "\n			", HTML.Raw("<h3>Methode</h3>"), "\n			", HTML.BUTTON({
    "class": "ui fluid huge button btn-mode active primary"
  }, "\n			", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "lesen");
  }, function() {
    return [ "\n				", HTML.I({
      "class": "icon book"
    }), " Lesen\n			" ];
  }), "\n			", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "wort");
  }, function() {
    return [ "\n				", HTML.I({
      "class": "icon adjust"
    }), " Rate Wort\n			" ];
  }), "\n			", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "definition");
  }, function() {
    return [ "\n				", HTML.I({
      "class": "icon adjust horizontally flipped"
    }), " Rate Definition\n			" ];
  }), "\n			"), "\n		") ];
}));
