
Template.__checkName("trainer");
Template["trainer"] = new Template("Template.trainer", (function() {
  var view = this;
  return [ Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n	", HTML.DIV({
      "class": "ui container accordion"
    }, "\n	  ", HTML.DIV({
      "class": function() {
        return [ Blaze.If(function() {
          return Spacebars.dataMustache(view.lookup("getSession"), "settingsTrainer");
        }, function() {
          return "active";
        }), " ui header title btn-settings-trainer" ];
      }
    }, "\n	    ", HTML.I({
      "class": "dropdown blue icon"
    }), "\n	    Einstellungen\n	  "), "\n	  ", HTML.DIV({
      "class": function() {
        return [ Blaze.If(function() {
          return Spacebars.dataMustache(view.lookup("getSession"), "settingsTrainer");
        }, function() {
          return "active fadeInDown animated ";
        }), " content " ];
      }
    }, "\n\n			", HTML.DIV({
      "class": "ui container nav-trainer"
    }, "\n				", HTML.DIV({
      "class": "ui three column  very relaxed stackable grid"
    }, "\n					", HTML.DIV({
      "class": "column"
    }, "\n						", Spacebars.include(view.lookupTemplate("navModeTrainer")), "\n					"), "\n					", HTML.DIV({
      "class": "ui vertical divider"
    }, "\n						und\n					"), "\n					", HTML.DIV({
      "class": "column"
    }, "\n						", Spacebars.include(view.lookupTemplate("navSourceTrainer")), "\n					"), "\n					", HTML.DIV({
      "class": "ui vertical divider"
    }, "\n						und\n					"), "\n					", HTML.DIV({
      "class": "column"
    }, "\n						", Spacebars.include(view.lookupTemplate("navOrderTrainer")), "\n					"), "\n				"), "\n			"), "\n\n	  "), "\n	"), "\n	", HTML.DIV({
      "class": "ui container divider"
    }), "\n	" ];
  }), "\n\n", Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n\n		", HTML.BR(), "\n\n", HTML.DIV({
      "class": "ui container centered grid"
    }, "\n	", HTML.DIV({
      "class": "row"
    }, "\n		", HTML.DIV({
      "class": "three wide column"
    }, "\n\n			", Spacebars.include(view.lookupTemplate("backward")), "\n\n		"), "\n		", HTML.DIV({
      "class": "ten wide column"
    }, "\n\n			", Spacebars.include(view.lookupTemplate("trainerContent")), "\n\n			", Blaze.If(function() {
      return Spacebars.call(view.lookup("lengthIsOne"));
    }, function() {
      return [ "\n				", HTML.DIV({
        "class": "ui orange message text-center"
      }, "\n						", HTML.SPAN({
        "class": "flag animate-box fadeInUp animated"
      }, "\n							", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "navSourceFav");
      }, function() {
        return [ "\n								", HTML.H3("Letzter Favorit ", HTML.I({
          "class": "icon frown"
        })), "\n							" ];
      }), "\n							", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "navSourceNotFav");
      }, function() {
        return [ "\n								", HTML.H3("Letzter Begriff der nicht favorisiert ist."), "\n							" ];
      }), "\n						"), "\n				"), "\n			" ];
    }), "\n\n			", Blaze.Unless(function() {
      return Spacebars.call(view.lookup("isAlphabetic"));
    }, function() {
      return [ "\n				", HTML.P({
        "class": "ui orange message"
      }, "Tipp: Das gesuchte Wort enthaelt nur Zeichen des Alphabets (a - Z). Keine Leer- und Sonderzeichen."), "\n			" ];
    }), "\n			", Blaze.Unless(function() {
      return Spacebars.call(view.lookup("isLength64"));
    }, function() {
      return [ "\n				", HTML.P({
        "class": "ui orange message"
      }, "Tipp: Das gesuchte Wort enthaelt nicht mehr als 64 Zeichen."), "\n			" ];
    }), "\n\n		"), "\n		", HTML.DIV({
      "class": "three wide column"
    }, "\n\n		", Spacebars.include(view.lookupTemplate("forward")), "\n\n		"), "\n	"), "\n"), "\n" ];
  }, function() {
    return [ "\n	", Spacebars.include(view.lookupTemplate("loading")), "\n" ];
  }) ];
}));

Template.__checkName("trainerContent");
Template["trainerContent"] = new Template("Template.trainerContent", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n			", Spacebars.With(function() {
      return Spacebars.call(view.lookup("entry"));
    }, function() {
      return [ "\n				", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "lesen");
      }, function() {
        return [ "\n					", Spacebars.include(view.lookupTemplate("modusLesen")), "\n				" ];
      }), "\n				", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "wort");
      }, function() {
        return [ "\n					", Spacebars.include(view.lookupTemplate("modusWort")), "\n				" ];
      }), "\n				", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "definition");
      }, function() {
        return [ "\n					", Spacebars.include(view.lookupTemplate("modusDefinition")), "\n				" ];
      }), "\n				", Blaze.If(function() {
        return Spacebars.dataMustache(view.lookup("getSession"), "eingabe");
      }, function() {
        return [ "\n					", Spacebars.include(view.lookupTemplate("modusEingabe")), "\n				" ];
      }), "\n			" ];
    }), "\n		" ];
  }, function() {
    return [ "\n			", Spacebars.include(view.lookupTemplate("loading")), "\n		" ];
  });
}));
